body {
  font-family: 'Montserrat', sans-serif;
}

/* h1, h2, h3, h4, h5, h6 {
  font-family: 'Comfortaa', cursive;
} */

input.ant-input {
  border-radius: 50px;
  padding: 4px 15px;
}
span.ant-input-affix-wrapper {
  border-radius: 50px;
}
span.ant-input-affix-wrapper > input.ant-input {
  padding: 0 5px;
}

.full-heigh {
  height: 100%;
}

.site-layout-content {
  min-height: calc(100vh - 64px - 74px);
  padding: 0;
  background: #fff;
  overflow: hidden;
}
#components-layout-demo-top .logo {
  float: left;
  width: 120px;
  height: 31px;
  margin: 16px 24px 16px 0;
  background: rgba(255, 255, 255, 0.3);
}
.ant-row-rtl #components-layout-demo-top .logo {
  float: right;
  margin: 16px 0 16px 24px;
}

.ant-layout-header {
  background-color: #fff;
  width: 100%;
}

.ant-drawer-body {
  padding: 24px 0;
}

.ant-popover .ant-popover-title {
  padding: 10px 16px 8px;
}
#main-banner{height:calc(100vh - 0px);background:#00a1ba;background:linear-gradient(45deg, #00a1ba 0%, #9cd873 100%)}#main-banner h1{color:#fff;font-size:5em}#main-banner .iframe-container{overflow:hidden;pointer-events:none}#main-banner .iframe-container #iframeCover{background-image:url(https://res.cloudinary.com/djv6xyyqp/image/upload/v1614560922/rentit/images/floating-2_eet4xd.png);background-position:center;position:absolute;height:100%;width:100%;transition:opacity 2s}@media only screen and (min-width: 1700px){#main-banner .iframe-container #iframeCover{transform:scale(1.25)}}@media only screen and (min-width: 2000px){#main-banner .iframe-container #iframeCover{transform:scale(1.4)}}#main-banner .iframe-container iframe{opacity:0;transition:opacity 2s}@media only screen and (min-width: 1700px){#main-banner .iframe-container iframe{transform:scale(1.25)}}@media only screen and (min-width: 2000px){#main-banner .iframe-container iframe{transform:scale(1.4)}}#latest-prods .ant-skeleton-element{width:100%}

.main-row{min-height:inherit;background:#00a1ba;background:linear-gradient(45deg, #00a1ba 0%, #9cd873 100%);padding:30px 0}.main-row.auth-cont .ant-btn{margin-top:20px}.main-row>.ant-col>div{background-color:#fff;padding:50px 30px 40px 30px;border-radius:20px;box-shadow:0 0 20px 0 #00000040}.main-row>.ant-col>div h1.ant-typography{display:inline-block;font-size:4em;pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.main-row>.ant-col>div #socialDivider.ant-divider{margin-top:40px}.main-row>.ant-col>div .socialLogin .anticon{font-size:2.5em;color:rgba(0,0,0,0.85)}.main-row>.ant-col>div .socialLogin .changeAuth{margin-top:40px}

.profile-row{padding-left:16px;padding-right:16px}.profile-row .profile-cont{height:100%}.profile-row .profile-cont .ant-avatar .ant-avatar-string{font-size:3em}.profile-row .profile-cont.side-menu-cont .ant-list{margin-top:20px}.profile-row .profile-cont.side-menu-cont .ant-list .ant-btn{margin-top:0px}.profile-row .profile-cont.info-cont .user-info-header{display:flex;align-items:center;margin-bottom:0.5em}.profile-row .profile-cont.info-cont .user-info-header .user-score{margin-left:20px}.profile-row .profile-cont.info-cont .user-info-header .user-score h5{line-height:1}.profile-row .profile-cont.info-cont .user-info-header h5,.profile-row .profile-cont.info-cont .user-info-header h1{margin-bottom:0}.profile-row .profile-cont.info-cont .ant-divider-horizontal{margin:18px 0}.profile-row .profile-cont.info-cont h2.ant-typography{margin-top:0}

